.container {
    width: 100vw;
    min-width: 320px;
    margin: auto;
    display: flex;
    flex-direction: column;;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
};

.content_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: calc(100vw - 40px);
    margin: 0px auto;
    max-width: 1440px;
    padding: 80px 0px 80px 0px;
    z-index: 7;

    @media screen and (min-width: 768px) {
        width: 75vw;
    };

    @media screen and (min-width: 1024px) {
        padding: 30px 0px 80px 0px;
    };
};

.title {
    margin: 0px;
    text-align: center;
    color: #00A3FF;
    font-family: "Exo2-Bold";
    font-size: 36px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
};

.video_wrapper {
    min-width: 280px;
    width: 100%;
    position: relative;
    padding-top: 56.25%;
    margin-top: 30px;

    @media screen and (min-width: 1024px) {
        padding-top: 0px;
        width: 45vw;
        height: 60vh;
        max-width: 1008px;
        max-height: 550px;
    };
};

.react_player {
    position: absolute;
    top: 0;
    left: 0;
};


